@import "../../styles/variables.css";

.form {
    align-items: center;
    display: flex;
    flex-direction: column;
    /* max-width: 300px; */
    margin-top: 34px;
    gap: 40px;
}

.formMainCont {
    display: flex;
    flex-direction: column;
    gap: 32px;
}

.formLeftCont,
.formRightCont {
    display: flex;
    flex-direction: column;
    gap: 32px;
    max-width: 239px;
}

.bloodTypeContainer {
    display: flex;
    flex-direction: column;
}

.label {
    font-size: 14px;
    font-weight: bold;
    color: gray;
}

.radioGroup {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 10px;
}

/* Hide the actual radio input */
.radioInput {
    display: none;
    /* Hides the default radio input */
}

.radioLabel {
    position: relative;
    display: inline-flex;
    /* flex-direction: column; */
    align-items: center;
    cursor: pointer;
    gap: 8px;
    color: gray;
    font-size: 14px;
    margin: 0 8px;
    /* Add spacing between the labels */
    transition: color 0.3s ease;
}

.radioLabel::before {
    content: "";
    width: 22px;
    height: 22px;
    border: 2px solid gray;
    border-radius: 50%;
    background-color: transparent;
    transition: all 0.3s ease;
    /* Smooth transition */
    position: relative;
    display: inline-block;
}

/* Inner orange circle */
.radioLabel::after {
    content: "";
    width: 12px;
    height: 12px;
    background-color: var(--brand-color);
    border-radius: 50%;
    position: absolute;
    top: 7px;
    /* Center inside the outer circle */
    left: 7px;
    display: none;
    /* Initially hidden */
}

/* When the radio is selected */
.radioInput:checked+.radioLabel::before {
    border-color: var(--brand-color);
    /* Change border color to orange */
}

.radioInput:checked+.radioLabel::after {
    display: block;
    /* Show the orange inner circle */
}

.radioInput:checked+.radioLabel {
    color: var(--brand-color);
    /* Change text color */
}

.errorMessage {
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 15px;
    color: red;
    flex-wrap: wrap;
    font-size: 14px;
    font-weight: 500;
    justify-content: center;
    margin-top: 4px;
    text-align: center;
}



.link:hover {
    background: var(--brand-color);
    text-decoration: underline;
    scale: (1.02);
}

@media screen and (width >=768px) {
    .form {
        align-items: flex-start;
        gap: 45px;
        margin-top: 45px;
    }

    .errorMessage {
        font-size: 20px;
        margin: 0 auto;
    }

    .formMainCont {
        flex-direction: row;
        gap: 30px;
    }

    .formLeftCont,
    .formRightCont {
        gap: 40px;
    }

    .formRightCont {
        margin: 0;
    }
}